/* General Styles */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

input {
  font-size: 14px;
}

body {
  line-height: 1.4;
  font-family: Helvetica, Arial, sans-serif;
}

/* Chat Page Layout */

.chat {
  display: flex;
}

/* Chat styles */

.chat__main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  max-height: 100vh;
}

.chat__messages {
  flex-grow: 1;
  padding: 24px 24px 0 24px;
  overflow-y: auto;
}

/* Message Styles */

.message {
  margin-bottom: 16px;
}

.message__name {
  font-weight: 600;
  font-size: 14px;
  margin-right: 8px;
}

.message__meta {
  color: #777;
  font-size: 14px;
}

.message a {
  color: #0070cc;
}

/* Message Composition Styles */

.compose {
  display: flex;
  flex-shrink: 0;
  margin-top: 16px;
  padding: 24px;
}

.compose form {
  display: flex;
  flex-grow: 1;
  margin-right: 16px;
}

.compose input {
  border: 1px solid #eeeeee;
  width: 100%;
  padding: 12px;
  margin: 0 16px 0 0;
  flex-grow: 1;
}

.compose button {
  font-size: 14px;
}

/* Chat Sidebar Styles */

.room-title {
  font-weight: 400;
  font-size: 22px;
  background: #2c2f3a;
  padding: 24px;
}

.list-title {
  font-weight: 500;
  font-size: 18px;
  margin-bottom: 4px;
  padding: 12px 24px 0 24px;
}

.users {
  list-style-type: none;
  font-weight: 300;
  padding: 12px 24px 0 24px;
}
.chat {
  display: flex;
  flex-direction: column;
  flex: 0.65;
  background-color: #358fef;
}

.chat__header {
  padding: 19px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid lightgray;
}
.chat__headerInfo {
  flex: 1;
  padding-left: 20px;
}
.chat__headerInfo > h3 {
  margin-bottom: 3px;
  font-weight: 500;
}
.chat__body {
  flex: 1;
  background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
  background-repeat: repeat;
  background-position: center;
  padding: 30px;
  overflow: auto;
}
.chat__message {
  position: relative;
  font-size: 16px;
  padding: 10px 10px 12px 10px;
  border-radius: 10px;
  background-color: #ffffff;
  margin-bottom: 30px;
  width: fit-content;
}

.chat__receiver {
  margin-left: auto;
  background-color: #dcf8c6;
  width: fit-content;
  padding-bottom: 10px;
}
.chatContainer {
  display: flex;
  justify-content: space-between;
  margin-top: 4px;
}
.chat__timestamp {
  margin-left: 10px;
  font-size: x-small;
  padding-top: 8%;
}
.chat__mess {
  flex: 1;
}
.chat__name {
  display: block;
  font-weight: 800;
  font-size: 15px;
}
.chat__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 62px;
  border-top: 1px solid lightgray;
}

.chat__footer > form {
  flex: 1;
  display: flex;
}
.chat__footer > form > input {
  flex: 1;
  border-radius: 30px;
  padding: 10px;
  border: none;
  outline: none;
  margin-left: 10px;
}
.chat__footer > form > button {
  display: none;
}
.chat__footer > .MuiSvgIcon-root {
  padding: 10px;
  color: gray;
}
/* .onlineIcon{
  margin-bottom: 2px;
} */

.exit__button {
  color: red;
}

/* 
ToolTip CSS
*/
.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 80px;
  font-size: 15px;
  background-color: white;
  color: #000;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  top: -5px;
  right: 105%;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.centered-form__input {
  border: 1px solid #eeeeee;
  padding: 12px;
  outline: none;
}

.centered-form__button {
  cursor: pointer;
  padding: 12px;
  background: #358fef;
  border: none;
  color: white;
  font-size: 16px;
  transition: background 0.3s ease;
}

.centered-form__button:hover {
  background: #0375f0;
}

.centered-form__button:disabled {
  cursor: default;
  background: #7c5cbf94;
}

/* Join Page Styles */

.centered-form {
  background: cornflowerblue;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.centered-form__box {
  box-shadow: 0px 0px 17px 1px #1d1f26;
  background: #f7f7fa;
  padding: 24px;
  width: 350px;
}

.centered-form button {
  width: 100%;
}

.centered-form input {
  margin-bottom: 16px;
  width: 100%;
}
.centered-form__title {
  margin-bottom: 16px;
  width: inherit;
}

.white {
  color: white;
}
.userList {
  margin: auto;
  font-weight: 500;
  text-transform: capitalize;
}
.displayName {
  color: white;
  font-size: 20px;
  margin-top: 10px;
}
.groupIcon {
  color: #358fef;
  font-size: large;
  background-color: whitesmoke;
  border-radius: 20px;
}

.sidebarChat {
  display: flex;
  padding: 20px;
  cursor: pointer;
  border-bottom: 1px solid #f6f6f6;
}
.sidebarChat:hover {
  background-color: #ebebeb;
}
.sidebar__chatInfo {
  margin-left: 15px;
}

.sidebar__chatInfo > h2 {
  font-size: 16px;
  margin-bottom: 8px;
}

.sidebar {
  display: flex;
  flex-direction: column;
  flex: 0.35;
}
.sidebar__header {
  display: flex;
  justify-content: space-between;
  padding: 26px;
  border-right: 1px solid lightgray;
  background-color: #358fef;
}
.sidebar__headerRight {
  display: flex;
  justify-content: flex-end;
  min-width: 10vw;
}
.sidebar__headerRight > .MuiSvgIcon-root {
  margin-right: 2vw;
  font-size: 24px !important;
}
.sidebar__search {
  display: flex;
  align-items: center;
  background-color: #f6f6f6;
  height: 65px;
  padding: 12px;
}
.sidebar__searchContainer {
  display: flex;
  align-items: center;
  background-color: white;
  width: 100%;
  height: 35px;
  border-radius: 20px;
}
.sidebar__searchContainer > .MuiSvgIcon-root {
  padding-left: 5px;
}
.sidebar__searchContainer > input {
  border: none;
  outline-width: 0;
  margin-left: 10px;
  width: 100%;
}
.sidebar__chats {
  flex: 1;
  background-color: white;
  overflow: auto;
}
.memberIcon {
  display: none !important;
}

.chat__body .sidebar__chats,
.chat__body .sidebar__search {
  display: none;
}
.information {
  display: none;
}
/* 768 1024 */
@media only screen and (max-width: 600px) {
  .information {
    display: block;
    padding: 0 10%;
  }
}
@media only screen and (max-width: 820px) {
  .chat__body .sidebar__chats,
  .chat__body .sidebar__search {
    display: block;
  }
  .sidebarChat {
    display: flex;
    padding: 20px;
    cursor: pointer;
    border-bottom: 1px solid #f6f6f6;
  }
  .sidebarChat:hover {
    background-color: #ebebeb;
  }
  .sidebar__chatInfo {
    margin-left: 15px;
  }

  .sidebar__chatInfo > h2 {
    font-size: 16px;
    margin-bottom: 8px;
  }

  .sidebar {
    /* display: flex; */
    display: none;
    flex-direction: column;
    flex: 0.35;
  }
  .sidebar__header {
    display: flex;
    justify-content: space-between;
    padding: 26px;
    border-right: 1px solid lightgray;
    background-color: #358fef;
  }
  .sidebar__headerRight {
    display: flex;
    justify-content: flex-end;
    min-width: 10vw;
  }
  .sidebar__headerRight > .MuiSvgIcon-root {
    margin-right: 2vw;
    font-size: 24px !important;
  }
  .sidebar__search {
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 65px;
    padding: 12px;
  }
  .sidebar__searchContainer {
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
  }
  .sidebar__searchContainer > .MuiSvgIcon-root {
    padding-left: 5px;
  }
  .sidebar__searchContainer > input {
    border: none;
    outline-width: 0;
    margin-left: 10px;
    width: 100%;
  }
  .sidebar__chats {
    flex: 1;
    background-color: white;
    overflow: auto;
  }

  .memberIcon {
    display: inline-block !important;
  }
  .chat {
    flex: 1;
  }
}
@media only screen and (max-width: 430px) {
  .white {
    font-size: 12px;
  }
}
.errorPage {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.errorPage__link > button {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-decoration: none;
  background-color: #6c63ff;
  color: white;
  border-radius: 20px;
  padding: 10px;
  cursor: pointer;
  font-weight: 600;
  border-color: black;
}

@media only screen and (max-width: 400px) {
  .chat,
  .app__body,
  .app {
    overflow: auto;
  }

  .chat__footer > form {
    flex: 1 1;
    display: flex;
    overflow: hidden;
  }
  .centered-form__box {
    width: 95%;
  }
  .app {
    display: block;
  }
}
@media only screen and (max-width: 500px) {
  .signin-signup {
    display: flex;
    justify-content:space-between;
  }
  .title {
    position: absolute;
    bottom: 320%;
    left: 17%;
  }
}
