@media only screen and (min-width: 350px) {
  .app {
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: #dadbd3;
  }
  .app__body {
    display: flex;
    background-color: #ededed;
    height: 90vh;
    width: 90vw;
    box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.75);
  }
}
