    display: block;
  }
  .sidebarChat {
    display: flex;
    padding: 20px;
    cursor: pointer;
    border-bottom: 1px solid #f6f6f6;
  }
  .sidebarChat:hover {
    background-color: #ebebeb;
  }
  .sidebar__chatInfo {
    margin-left: 15px;
  }

  .sidebar__chatInfo > h2 {
    font-size: 16px;
    margin-bottom: 8px;
  }

  .sidebar {
    /* display: flex; */
    display: none;
    flex-direction: column;
    flex: 0.35 1;
  }
  .sidebar__header {
    display: flex;
    justify-content: space-between;
    padding: 26px;
    border-right: 1px solid lightgray;
    background-color: #358fef;
  }
  .sidebar__headerRight {
    display: flex;
    justify-content: flex-end;
    min-width: 10vw;
  }
  .sidebar__headerRight > .MuiSvgIcon-root {
    margin-right: 2vw;
    font-size: 24px !important;
  }
  .sidebar__search {
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 65px;
    padding: 12px;
  }
  .sidebar__searchContainer {
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
  }
  .sidebar__searchContainer > .MuiSvgIcon-root {
    padding-left: 5px;
  }
  .sidebar__searchContainer > input {
    border: none;
    outline-width: 0;
    margin-left: 10px;
    width: 100%;
  }
  .sidebar__chats {
    flex: 1 1;
    background-color: white;
    overflow: auto;
  }

  .memberIcon {
    display: inline-block !important;
  }
  .chat {
    flex: 1 1;
  }
}
@media only screen and (max-width: 430px) {
  .white {
    font-size: 12px;
  }
}
.errorPage {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.errorPage__link > button {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-decoration: none;
  background-color: #6c63ff;
  color: white;
  border-radius: 20px;
  padding: 10px;
  cursor: pointer;
  font-weight: 600;
  border-color: black;
}

@media only screen and (max-width: 400px) {
  .chat,
  .app__body,
  .app {
    overflow: auto;
  }

  .chat__footer > form {
    flex: 1 1;
    display: flex;
    overflow: hidden;
  }
  .centered-form__box {
    width: 95%;
  }
  .app {
    display: block;
  }
}
@media only screen and (max-width: 500px) {
  .signin-signup {
    display: flex;
    justify-content:space-between;
  }
  .title {
    position: absolute;
    bottom: 320%;
    left: 17%;
  }
}

@media only screen and (min-width: 350px) {
  .app {
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: #dadbd3;
  }
  .app__body {
    display: flex;
    background-color: #ededed;
    height: 90vh;
    width: 90vw;
    box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.75);
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
input {
  font-family: "Poppins", sans-serif;
}

.container {
  position: relative;
  width: 100%;
  background-color: #fff;
  min-height: 100vh;
  overflow: hidden;
}

.forms-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.signin-signup {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 75%;
  width: 50%;
  transition: 1s 0.7s ease-in-out;
  display: grid;
  grid-template-columns: 1fr;
  z-index: 5;
}

.formclass {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0rem 5rem;
  transition: all 0.2s 0.7s;
  overflow: hidden;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
}

.formclass.sign-in-form {
  z-index: 2;
}

.title {
  font-size: 2.2rem;
  color: #444;
  margin-bottom: 10px;
}

.btn {
  width: 150px;
  background-color: #5995fd;
  border: none;
  outline: none;
  height: 49px;
  border-radius: 49px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
  margin: 10px 0;
  cursor: pointer;
  transition: 0.5s;
}

.btn:hover {
  background-color: #4d84e2;
}
.panels-container {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.container:before {
  content: "";
  position: absolute;
  height: 2000px;
  width: 2000px;
  top: -10%;
  right: 48%;
  transform: translateY(-50%);
  background-image: linear-gradient(-45deg, #4481eb 0%, #04befe 100%);
  transition: 1.8s ease-in-out;
  border-radius: 50%;
  z-index: 6;
}

.image {
  width: 100%;
  transition: transform 1.1s ease-in-out;
  transition-delay: 0.4s;
}

.panel {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  text-align: center;
  z-index: 6;
}

.left-panel {
  pointer-events: all;
  padding: 3rem 17% 2rem 12%;
}


@media (max-width: 870px) {
  .container {
    min-height: 800px;
    height: 100vh;
  }
  .signin-signup {
    width: 100%;
    top: 95%;
    transform: translate(-50%, -100%);
    transition: 1s 0.8s ease-in-out;
  }

  .signin-signup,
  .container.sign-up-mode .signin-signup {
    left: 50%;
  }

  .panels-container {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr 1fr;
  }

  .panel {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2.5rem 8%;
    grid-column: 1 / 2;
  }

  .left-panel {
    grid-row: 1 / 2;
  }

  .image {
    width: 200px;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.6s;
  }

  .panel .content {
    padding-right: 15%;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.8s;
  }

  .panel h3 {
    font-size: 1.2rem;
  }

  .panel p {
    font-size: 0.7rem;
    padding: 0.5rem 0;
  }

  .btn.transparent {
    width: 110px;
    height: 35px;
    font-size: 0.7rem;
  }

  .container:before {
    width: 1500px;
    height: 1500px;
    transform: translateX(-50%);
    left: 30%;
    bottom: 68%;
    right: auto;
    right: initial;
    top: auto;
    top: initial;
    transition: 2s ease-in-out;
  }
}

@media (max-width: 570px) {
  .formclass {
    padding: 0 1.5rem;
  }

  .image {
    display: none;
  }
  .panel .content {
    padding: 0.5rem 1rem;
  }
  .container {
    padding: 1.5rem;
  }

  .container:before {
    bottom: 72%;
    left: 50%;
  }
}

